import * as React from "react";
import PropTypes from "prop-types";
import useScrollTrigger from "@mui/material/useScrollTrigger";

import {
  AppBar,
  Box,
  Container,
  IconButton,
  Menu,
  MenuItem,
  Toolbar,
  Typography,
} from "@mui/material";

import { colors } from "../../utils/colors";

import Logo from "../logo";
import TransitionLink from "../transition-link";
import { Link } from "../typography";
import { info } from "../../utils/info";

function ElevationScroll(props) {
  const { children, window } = props;

  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
    target: window ? window() : undefined,
  });

  return React.cloneElement(children, {
    elevation: trigger ? 4 : 0,
  });
}

ElevationScroll.propTypes = {
  children: PropTypes.element.isRequired,
  window: PropTypes.func,
};

const MenuText = ({ children }) => {
  return (
    <Typography
      sx={{
        color: colors.white,
        fontSize: { xs: "14px", md: "18px" },
        letterSpacing: { xs: "1.5px", md: 1.75 },
        padding: { xs: 2, md: 2 },
        fontWeight: 400,
        textTransform: "uppercase",
        lineHeight: "normal",
        transition: ".2s all",
      }}
    >
      {children}
    </Typography>
  );
};

export default function Header(props) {
  const [openMenu, setOpenMenu] = React.useState(null);

  return (
    <React.Fragment>
      <ElevationScroll {...props}>
        <AppBar>
          <Container maxWidth="lg">
            <Toolbar
              disableGutters
              sx={{
                justifyContent: "space-between",
                minHeight: { xs: "80px", md: "96px" },
              }}
            >
              <TransitionLink to="/" direction="left">
                <Logo svgColor={colors.themeColor} svgWidth="48px" />
              </TransitionLink>
              <Box
                sx={{
                  justifyContent: "flex-end",
                }}
              >
                <MenuItem>
                  <Link href={info.whatsapp.link} sx={{ background: "red" }}>
                    <MenuText>Contato</MenuText>
                  </Link>
                </MenuItem>
              </Box>
            </Toolbar>
          </Container>
        </AppBar>
      </ElevationScroll>
      <Toolbar />
    </React.Fragment>
  );
}
