import React from "react";
import styled from "styled-components";
import { Typography } from "@mui/material";

import { colors } from "../../utils/colors";

const LinkStyled = styled.a`
  color: ${(props) => props.color || colors.white};
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 1.75px;
  text-transform: uppercase;
  text-decoration: none;
  display: flex;
  align-items: center;
  cursor: pointer;
`;

const Link = ({ href, children, color }) => {
  return (
    <LinkStyled href={href} target="_blank" rel="noopener" color={color}>
      {children}
    </LinkStyled>
  );
};

const H1 = ({ children }) => {
  return (
    <Typography
      variant="h1"
      sx={{
        textTransform: "uppercase",
        fontWeight: 300,
        letterSpacing: 2,
        fontSize: "3rem",
      }}
    >
      {children}
    </Typography>
  );
};

const H2 = ({ children }) => {
  return (
    <Typography
      variant="h2"
      sx={{
        textTransform: "uppercase",
        fontSize: "2.75rem",
        fontWeight: 300,
        letterSpacing: 2,
      }}
    >
      {children}
    </Typography>
  );
};

const Paragraph = ({ children }) => {
  return (
    <Typography
      sx={{
        fontSize: "21px",
        margin: "24px 0 0 0",
        fontWeight: 300,
      }}
    >
      {children}
    </Typography>
  );
};

export { H1, H2, Link, Paragraph };
