import React from "react";

import Brightness4Icon from "@mui/icons-material/Brightness4";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import Circle from "@mui/icons-material/Circle";
import MenuIcon from "@mui/icons-material/Menu";

import PlaceOutlinedIcon from "@mui/icons-material/PlaceOutlined";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";

export function useIcon(name, props) {
  switch (name) {
    case "brightness4":
      return <Brightness4Icon sx={{ ...props }} />;
    case "brightness7":
      return <Brightness7Icon sx={{ ...props }} />;
    case "circle":
      return <Circle sx={{ ...props }} />;
    case "menu":
      return <MenuIcon sx={{ ...props }} />;
    case "map":
      return <PlaceOutlinedIcon sx={{ ...props }} />;
    case "whatsapp":
      return <WhatsAppIcon sx={{ ...props }} />;
    default:
      return <></>;
  }
}

export function useInterval(callback, delay) {
  const savedCallback = React.useRef();

  React.useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);

  React.useEffect(() => {
    const id = setInterval(() => {
      savedCallback.current();
    }, delay);

    return () => clearInterval(id);
  }, [delay]);
}
