import React from "react";
import { Helmet } from "react-helmet";
import { useStaticQuery, graphql } from "gatsby";

import Favicon from "../../assets/images/favicon.png";

const Seo = ({ data }) => {
  const lang = "pt-BR";

  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
          }
        }
      }
    `
  );

  const { title, description, meta, image } = data;

  const defaultTitle = `${site.siteMetadata?.title} | ${title}`;
  const metaDescription = `${site.siteMetadata?.title} | ${description}`;

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      title={defaultTitle}
      defaultTitle={defaultTitle}
      description={metaDescription}
      meta={[
        {
          name: "description",
          content: metaDescription,
        },
        {
          name: "image",
          content: image,
        },
        {
          property: "og:title",
          content: title,
        },
        {
          property: "og:description",
          content: metaDescription,
        },
        {
          property: "og:type",
          content: "website",
        },
        {
          property: "og:image",
          content: image,
        },
        {
          name: "twitter:card",
          content: "summary_large_image",
        },
        {
          name: "twitter:creator",
          content: site.siteMetadata?.social?.twitter || "",
        },
        {
          name: "twitter:title",
          content: title,
        },
        {
          name: "twitter:description",
          content: metaDescription,
        },
      ].concat(meta)}
      link={[
        {
          rel: "icon",
          type: "image/png",
          href: Favicon,
        },
      ]}
    />
  );
};

export default Seo;
